import * as type from './actions/actionTypes'

const initialState = {
  isDataLoaded: true,
  isModalOpen: false,
  userInfo: {},
  productList: [],
  shoppingCartList: [],
  modalType: '',
  selectProduct: {},
  selectedSpecList: []
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case type.GET_USER_INFO:
      return {
        ...state,
        userInfo: {...action.userInfo}
      }
    case type.GET_PRODUCT_LIST:
      return {
        ...state,
        isDataLoaded: true,
        productList: action.payload.productList
      }
    case type.GET_SHOPPTING_CART_LIST:
    case type.ADD_SHOPPTING_CART_ITEM:
    case type.DELETE_SHOPPTING_CART_ITEM:
      return {
        ...state,
        isDataLoaded: true,
        shoppingCartList: action.payload.shoppingCartList
      }
    case type.ADD_LOCAL_SHOPPTING_CART_ITEM:
      return {
        ...state,
        isDataLoaded: true,
        shoppingCartList: [
          ...state.shoppingCartList,
          action.payload.product
        ]
      }
    case type.DELETE_LOCAL_SHOPPTING_CART_ITEM:
      const cartIndex = state.shoppingCartList.findIndex(shoppingCart => shoppingCart.cart.timestamp === action.payload.product.cart.timestamp)
      const newShoppingCartList = [...state.shoppingCartList]
      if (cartIndex !== -1) {
        newShoppingCartList[cartIndex] = action.payload.product
      }

      return {
        ...state,
        isDataLoaded: true,
        shoppingCartList: newShoppingCartList
      }
    case type.SET_DATA_LOADED:
      return {
        ...state,
        isDataLoaded: action.payload.isDataLoaded
      }
    case type.TOGGLE_MODAL:
      return {
        ...state,
        selectSpecList: [],
        selectedSpecList: (state.isModalOpen ? [] : state.selectedSpecList),
        selectProduct: (state.isModalOpen ? {} : state.selectProduct),
        isModalOpen: !state.isModalOpen
      }
    case type.UPDATE_MODAL_TYPE:
      return {
        ...state,
        modalType: action.payload.modalType
      }
    case type.SET_SELECT_PRODUCT:
      return {
        ...state,
        selectProduct: action.payload.selectProduct
      }
    case type.UPDATE_SELECT_SPEC_LIST:
      const currentIndex = state.selectedSpecList.indexOf(action.payload.selectSpec)
      const newSelectedSpecList = [...state.selectedSpecList]

      if (currentIndex === -1) {
        newSelectedSpecList.push(action.payload.selectSpec)
      } else {
        newSelectedSpecList.splice(currentIndex, 1)
      }
      return {
        ...state,
        selectedSpecList: newSelectedSpecList
      }
    default:
      return state
  }
}

export default reducer



