import Loadable from 'react-loadable'
import * as CONSTANT from './constant'

import AppLoading from './container/AppLoading'

const lazyloadComponent = (componentName) => {
  return Loadable({
    loader: () => import(`./container/${componentName}`),
    loading: AppLoading
  })
}

const RouteConfig = [
  {
    page: 'Product List',
    path: '/ProductList',
    component: lazyloadComponent('ProductList'),
    constant: CONSTANT.PAGE.PRODUCT_LIST
  },
  {
    page: 'Cart',
    path: '/ShoppingCart',
    component: lazyloadComponent('ShoppingCart'),
    constant: CONSTANT.PAGE.SHOPPING_CART
  }
]

export default RouteConfig



