import React from 'react'
import { Link } from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'
import AppHeader from './Common/AppHeader/AppHeader'
import AppBar from '@material-ui/core/AppBar'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Badge from '@material-ui/core/Badge'
import RouteConfig from '../RouteConfig'

const styles = theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper
  },
  viewOnGithub: {
    position: 'fixed',
    top: 0,
    right: 0,
    height: 64,
    zIndex: 1,
    [theme.breakpoints.only('xs')]: {
      height: 56
    }
  },
  padding: {
    padding: `0 ${theme.spacing.unit * 2}px`
  }
})

class AppNavContent extends React.Component {
  constructor () {
    super()
    const currentRoute = this.getCurrentRoute()
    this.state = {
      value: (currentRoute.index === -1) ? 0 : currentRoute.index
    }
  }

  componentDidMount () {
    const currentRoute = this.getCurrentRoute()
    const {setCurrentRoute} = this.props
    setCurrentRoute(currentRoute.constant)
  }

  getCurrentRoute = () => {
    let routeIndex = RouteConfig.findIndex((route, index) => `#${route.path}` === window.location.hash)
    if (routeIndex === -1) {
      routeIndex = 0
    }

    return {
      constant: RouteConfig[routeIndex].constant,
      index: routeIndex
    }
  }

  handleChange = (event, value) => {
    const {setCurrentRoute} = this.props
    const dataType = event.currentTarget.dataset.constant
    setCurrentRoute(dataType)
    this.setState({ value })
  }

  render () {
    const { classes, shoppingCartList } = this.props
    const { value } = this.state
    const countCartProduct = shoppingCartList.length

    return (
      <div className={classes.root}>
        <AppBar>
          <AppHeader />
          <Tabs value={value} onChange={this.handleChange} centered>
            {RouteConfig.map(route => {
              return <Tab
                label={
                  route.page === 'Cart' && countCartProduct
                    ? (
                      <Badge
                        className={classes.padding}
                        color='secondary'
                        badgeContent={countCartProduct}>
                        {route.page}
                      </Badge>
                    )
                    : route.page
                }
                component={Link}
                to={route.path}
                key={route.page}
                data-constant={route.constant} />
            })}
          </Tabs>
          <a href='https://github.com/sky172839465/the-f2e-ch04-product-gallery' target='block'>
            <img
              className={classes.viewOnGithub}
              src={`https://sky172839465.github.io/the-f2e/img/view-on-github.black.png?now=${Date.now()}`}
              alt='View on github' />
          </a>
        </AppBar>
      </div>
    )
  }
}

export default withStyles(styles)(AppNavContent)



