import * as actionTypes from './actionTypes'

export const setDataLoaded = (isDataLoaded) => {
  return {
    type: actionTypes.SET_DATA_LOADED,
    payload: {
      isDataLoaded
    }
  }
}

export const setCurrentRoute = (currentRoute) => {
  return {
    type: actionTypes.SET_CURRENT_ROUTE,
    payload: {currentRoute}
  }
}

export const toggleModal = () => {
  return {
    type: actionTypes.TOGGLE_MODAL
  }
}

export const updateModalType = (modalType) => {
  return {
    type: actionTypes.UPDATE_MODAL_TYPE,
    payload: {
      modalType
    }
  }
}

export const setSelectProduct = (selectProduct) => {
  return {
    type: actionTypes.SET_SELECT_PRODUCT,
    payload: {
      selectProduct
    }
  }
}

export const updateSelectSpecList = (selectSpec) => {
  return {
    type: actionTypes.UPDATE_SELECT_SPEC_LIST,
    payload: {
      selectSpec
    }
  }
}

export const addLocalShoppingCartItem = (product) => {
  return {
    type: actionTypes.ADD_LOCAL_SHOPPTING_CART_ITEM,
    payload: {
      product
    }
  }
}

export const deleteLocalShoppingCartItem = (product) => {
  return {
    type: actionTypes.DELETE_LOCAL_SHOPPTING_CART_ITEM,
    payload: {
      product
    }
  }
}



