import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://us-central1-the-f2e-service.cloudfunctions.net'

export const getGithubInfo = () => {
  return (dispatch) => {
    fetch('https://api.github.com/users/sky172839465')
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_USER_INFO,
          userInfo: data
        })
      })
  }
}

export const getProductList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/getProductList?isPublish=true`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_PRODUCT_LIST,
          payload: {
            productList: data.result
          }
        })
      })
  }
}

export const getShoppingCartList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/getShoppingCartList`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_SHOPPTING_CART_LIST,
          payload: {
            shoppingCartList: data.result
          }
        })
      })
  }
}

export const addShoppingCartItem = (product) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/addShoppingCartItem`, {
      method: 'post',
      body: JSON.stringify({product})
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.ADD_SHOPPTING_CART_ITEM,
          payload: {
            shoppingCartList: data.result
          }
        })
      })
  }
}

export const deleteShoppingCartItem = (product) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/deleteShoppingCartItem`, {
      method: 'post',
      body: JSON.stringify({
        productKey: product.cart.key
      })
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.DELETE_SHOPPTING_CART_ITEM,
          payload: {
            shoppingCartList: data.result
          }
        })
      })
  }
}



